/* Container to hold both the size select and the button side by side */
.product-container {
    display: flex;
    align-items: center;  /* Vertically center the items */
    gap: 15px;            /* Add space between the dropdown and the button */
    justify-content: center; /* Center both elements horizontally */
    flex-wrap: wrap; /* Ensure items wrap on smaller screens */
  }
  
  /* Add to Cart Button */
  .add-to-cart-button {
    background-color: black;
    color: white;
    border: 2px solid white;
    padding: 12px 24px;
    font-size: 12px;
    font-weight: bold;
    cursor: pointer;
    border-radius: 8px;
    text-transform: uppercase;
    transition: all 0.3s ease;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.15);
    width: 180px; /* Set a fixed width */
  }
  
  .add-to-cart-button:hover {
    background-color: #444;
    border-color: #666;
    transform: translateY(-3px);
  }
  
  /* Size Selection Dropdown */
  .size-select {
    background-color: black;
    color: white;
    border: 2px solid white;
    padding: 12px;
    font-size: 12px;
    font-weight: bold;
    border-radius: 8px;
    text-transform: uppercase;
    appearance: none; /* Remove default arrow */
    cursor: pointer;
    transition: border-color 0.3s ease;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.15);
    width: 180px; /* Set a fixed width */
  }
  
  .size-select:focus {
    outline: none;
    border-color: #666;
  }
  
  .size-select option {
    background-color: #222;
    color: white;
  }
  
  /* Optional: Arrow for dropdown */
  .size-select::after {
    content: 'â–¼';
    position: absolute;
    right: 15px;
    pointer-events: none;
  }
  .modal-overlay {
    overflow-y: scroll;
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: rgba(0, 0, 0, 0.6);
    display: flex;
    align-items: center;
    justify-content: center;
    z-index: 1000;
  }
  

.modal-content {
  background-color: #fff;
  padding: 20px;
  border-radius: 8px;
  width: 400px;
  max-width: 90%;
  position: relative;
  text-align: center;
}

.close-button {
  position: absolute;
  top: 10px;
  right: 10px;
  background-color: #ff4d4f;
  color: white;
  border: none;
  border-radius: 50%;
  width: 30px;
  height: 30px;
  cursor: pointer;
}

.modal-image {
  max-width: 60%;
  height: 80%;
  border-radius: 10px;
  margin-bottom: 20px;
}

.size-select,
.color-select {
  margin: 10px 0;
  padding: 5px;
  width: 100%;
}

  
  /* For responsiveness, if the screen is small, stack the items vertically */
  @media (max-width: 600px) {
    .product-container {
      flex-direction: column; /* Stack items vertically */
      align-items: flex-start; /* Align items to the left */
    }
  
    .size-select,
    .add-to-cart-button {
      width: 100%; /* Make buttons full width on small screens */
    }
  }
  